'use strict';

define(['knockout', 'vendor/lski-modal'], function (ko, modal) {

	"use strict";

	ko.bindingHandlers.modal = {
		update: function update(element, valueAccessor) {

			var val = ko.unwrap(valueAccessor());

			if (val) {
				modal.show(element);
			} else {
				modal.hide(element);
			}
		}
	};
});